.cart-dropdown {
    position: absolute;
    width: 40vh;
    height: 50vh;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid black;
    background-color: white;
    top: 90px;
    right: 10px;
    z-index: 5;
}
  
.login-form{
    width: 35%;
    padding: 30px;
    height: auto;
    padding-bottom: 60px;
    margin-top: 10vh;
    /* margin-bottom: 15vh; */
    display: flex;
    flex-direction: column;
    justify-content: left;
    box-shadow: 0 4px 8px 0 #1ef0874f, 0 6px 20px 0 rgba(0, 0, 0, 0.19);    
    border-radius: 2%;
    background-color: white;
     /* box-shadow: 0 4px 8px 0 #b8f8d8, 0 5px 20px 0 #b9f8da; */

}

.login-form h2{
    margin-bottom: 20px;
}



.form-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(0, 0, 0, 0) !important;
    height: auto;
    padding-bottom: 5%;
}

.form-container input,label{
    font-size: small;
}

.form-container label{
    font-weight: 500;
}


.shiftToLeft{
    width: 100%;
    display: flex;
    justify-content: left;

}
.loginLogo{width:60px;height:60px;margin-top:5vh;position:absolute}

